<template lang="html">
  <div class="text-area">
    <div class="text-input">
      <textarea resize="none" class="" :placeholder="text"></textarea>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    text: {
      type: String,
      require: true
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';

.text-area {
  background: @whiteColor;
}
.text-input {
  margin: 0 .22rem;
  padding-bottom: .2rem;
  textarea {
    width: 100%;
    height: 3.2rem;
    font-size: .32rem;
    padding: .2rem .2rem 0;
    color: @lightColor;
    border-color: @lineColor;
    border-radius: .08rem;
    &::placeholder {
      color: @lightColor;
    }
  }
}
</style>



