    <white-title text="举报原因"></white-title>
    <!-- 表单 -->
    <form method="post">
      <input-area text="请输入您的举报详情"></input-area>
      <foot-btn></foot-btn>
    </form>
  </div>
</template>

<script>
import defaultHead from '@/components/head/defaultHead'
import lightTitle from '@/components/common/lightTitle'
import whiteTitle from '@/components/common/whiteTitle'
import inputArea from '@/components/common/inputArea'
import footBtn from '@/components/common/footBtn'

export default {
  components: { defaultHead, lightTitle, whiteTitle, inputArea, footBtn }
}
</script>

<style lang="less" scoped>
.sale-report {
  padding-top: .88rem;

}
</style>



